'use client';

import { usePlant } from '@/contexts/PlantContext';
import { motion, AnimatePresence } from 'framer-motion';

const Plant = () => {
  const { level, progressPercentage, nextStageThreshold, currentStage, trades } = usePlant();
  
  // Plant stages with SVG paths for each growth stage
  const getPlantSvg = () => {
    const plantStages = {
      seed: (
        <svg key="seed" viewBox="0 0 100 200" className="w-32 h-48">
          <path d="M50 150 Q60 130 70 150 Q80 170 50 170 Q20 170 30 150 Q40 130 50 150 Z" fill="#8B5A2B" />
          <circle cx="50" cy="140" r="8" fill="#A0522D" />
        </svg>
      ),
      sprout: (
        <svg key="sprout" viewBox="0 0 100 200" className="w-32 h-48">
          <path d="M50 170 L50 100" stroke="#8B4513" strokeWidth="4" />
          <path d="M30 140 L50 120 L70 140" stroke="#228B22" strokeWidth="3" fill="none" />
          <path d="M50 120 Q60 100 70 110 Q80 120 70 130 Q60 140 50 130 Q40 140 30 130 Q20 120 30 110 Q40 100 50 120 Z" fill="#2E8B57" />
        </svg>
      ),
      flowering: (
        <svg key="flowering" viewBox="0 0 100 200" className="w-32 h-48">
          <path d="M50 170 L50 60" stroke="#8B4513" strokeWidth="5" />
          <path d="M20 90 L80 80 M10 130 L90 110 M30 60 L70 120" stroke="#228B22" strokeWidth="3" />
          <path d="M50 50 Q90 20 50 10 Q10 20 50 50 Z" fill="#2E8B57" />
          <path d="M50 100 Q100 70 50 60 Q0 70 50 100 Z" fill="#3CB371" />
          {/* Flowers */}
          <circle cx="30" cy="60" r="8" fill="#FF69B4" />
          <circle cx="70" cy="80" r="8" fill="#FF69B4" />
          <circle cx="20" cy="110" r="8" fill="#FF69B4" />
        </svg>
      ),
      fruiting: (
        <svg key="fruiting" viewBox="0 0 100 200" className="w-32 h-48">
          <path d="M45 170 L45 50 L55 50 L55 170 Z" fill="#8B4513" />
          <path d="M50 30 Q90 0 50 0 Q10 0 50 30 Z" fill="#2E8B57" />
          <path d="M50 70 Q100 30 50 20 Q0 30 50 70 Z" fill="#3CB371" />
          <path d="M50 110 Q100 80 50 60 Q0 80 50 110 Z" fill="#2E8B57" />
          <path d="M50 150 Q90 120 50 100 Q10 120 50 150 Z" fill="#3CB371" />
          {/* Fruits */}
          <circle cx="30" cy="50" r="6" fill="#FF4500" />
          <circle cx="70" cy="60" r="6" fill="#FF4500" />
          <circle cx="30" cy="100" r="6" fill="#FF4500" />
          <circle cx="70" cy="130" r="6" fill="#FF4500" />
          <circle cx="40" cy="150" r="6" fill="#FF4500" />
        </svg>
      )
    };

    return plantStages[currentStage];
  };

  const getStageName = () => {
    const names = {
      seed: 'Seed',
      sprout: 'Sprout',
      flowering: 'Flowering Tree',
      fruiting: 'Fruit Tree'
    };
    return names[currentStage];
  };

  const getNextStageName = () => {
    if (currentStage === 'seed') return 'Sprout';
    if (currentStage === 'sprout') return 'Flowering Tree';
    if (currentStage === 'flowering') return 'Fruit Tree';
    return 'Maximum Growth';
  };

  return (
    <div className="flex flex-col items-center justify-center p-6">
      <div className="relative w-full flex flex-col items-center mb-6">
        <div className="w-48 h-64 flex items-end justify-center relative">
          <AnimatePresence mode="wait">
            <motion.div
              key={`plant-${currentStage}`}
              initial={{ y: 20, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              exit={{ y: -20, opacity: 0 }}
              transition={{ duration: 0.5 }}
              className="absolute bottom-0"
            >
              {getPlantSvg()}
            </motion.div>
          </AnimatePresence>
        </div>

        <div className="mt-4 text-center">
          <div className="text-lg font-medium text-gray-800">{getStageName()}</div>
          <div className="text-sm text-gray-500">Level {level}</div>
          <div className="text-xs text-gray-400">{trades} trades completed</div>
        </div>
      </div>

      {/* Progress to next stage */}
      <div className="w-full max-w-xs mb-2">
        <div className="flex justify-between text-xs text-gray-500 mb-1">
          <span>Progress to {getNextStageName()}</span>
          <span>{progressPercentage}%</span>
        </div>
        <div className="w-full bg-gray-200 rounded-full h-2.5">
          <motion.div
            className="bg-gradient-to-r from-green-400 to-green-600 h-2.5 rounded-full"
            initial={{ width: '0%' }}
            animate={{ width: `${progressPercentage}%` }}
            transition={{ duration: 1 }}
          />
        </div>
      </div>

      <p className="text-sm text-gray-500 text-center mt-2 px-4">
        {getLevelMessage(level, currentStage)}
      </p>
    </div>
  );
};

const getLevelMessage = (level: number, stage: string): string => {
  const messages = {
    seed: [
      'Your seed has been planted!',
      'Keep going! Your seed is taking root.',
      'Almost there! Your plant is about to sprout.'
    ],
    sprout: [
      'Your plant has sprouted!',
      'Your sprout is growing stronger!',
      'Your plant is getting ready to bloom!'
    ],
    flowering: [
      'Beautiful! Your plant is flowering!',
      'Your flowers are attracting attention!',
      'Your plant is almost ready to bear fruit!'
    ],
    fruiting: [
      'Amazing! Your plant is bearing fruit!',
      'Your fruit tree is thriving!',
      'You\'ve reached the final stage!'
    ]
  };

  const stageMessages = messages[stage as keyof typeof messages] || [];
  const index = Math.min(Math.floor(level / 2), stageMessages.length - 1);
  return stageMessages[Math.max(0, index)] || 'Keep growing your skills!';
};

export default Plant;
